import React, { useEffect, useState } from "react";
import {AppBar,Box,Toolbar,Typography,Menu,IconButton,Container,Avatar,Button,Tooltip,MenuItem} from "@mui/material";
import MenuIcon from "@mui/icons-material/Menu";
import AdbIcon from "@mui/icons-material/Adb";
import { AWATAR_URL } from "../../utils/constants";
import { auth } from "../../utils/firebase";
import { signOut } from "firebase/auth";
import { Link, useNavigate } from "react-router-dom";
import { useSelector } from "react-redux";
import SearchIcon from '@mui/icons-material/Search';


const pages = ["Home", "TV Show", "Movies", "New & Popular"];
const settings = ["Profile", "Logout"];



function NetFlixHeader() {
  const [anchorElNav, setAnchorElNav] = useState(null);
  const [anchorElUser, setAnchorElUser] = useState(null);
  const [scrolled, setScrolled] = useState(false);

  const navigate = useNavigate();
  const user = useSelector(store => store.user);

  const handleOpenNavMenu = (event) => {
    setAnchorElNav(event.currentTarget);
  };

  const handleOpenUserMenu = (event) => {
    setAnchorElUser(event.currentTarget);
  };

  const handleCloseNavMenu = () => {
    setAnchorElNav(null);
  };

  const handleCloseUserMenu = () => {
    setAnchorElUser(null);
  };

  const handleSignOut = () => {
    signOut(auth)
      .then(() => {})
      .catch((error) => {
        navigate("/error")
      });
  };
  const handleSettingClick = (setting) => {
    if (setting === 'Logout') {
      handleSignOut();
    }
  };
 const handleNavigation = (page) =>{
  if(page === "Home"){
    navigate("/browse");
  }else(
    handleCloseNavMenu()
  )
 }

  

  useEffect(() => {
    const handleScroll = () => {
      const isScrolled = window.scrollY > 0;
      if (isScrolled !== scrolled) {
        setScrolled(isScrolled);
      }
    };

    window.addEventListener('scroll', handleScroll);
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, [scrolled]);

  return (
    
      <AppBar className={scrolled ? "netflix-header scrolled" : "netflix-header"}>
        <Container maxWidth="xl">
          <Toolbar disableGutters>
            <AdbIcon
              sx={{
                display: { xs: "none", md: "flex" },
                mr: 1,
                color: "#ec0707",
                fontSize:{md:"1.7rem"},
              }}
            />
            <Link to="/browse" style={{textDecoration:"none"}}>
            <Typography
              variant="h6"
              noWrap
              sx={{
                mr: 2,
                display: { xs: "none", md: "flex" },
                fontFamily: "monospace",
                fontWeight:"bold",
                letterSpacing: ".3rem",
                fontSize:{md:"1.7rem"},
                color: "#ec0707",
                textDecoration: "none",
              }}
            >
              NETFLIXGPT
            </Typography>
            </Link>

            <Box sx={{ flexGrow: 1, display: { xs: "flex", md: "none" } }}>
              <IconButton
                size="large"
                aria-label="account of current user"
                aria-controls="menu-appbar"
                aria-haspopup="true"
                onClick={handleOpenNavMenu}
                color="inherit"
              >
                <MenuIcon sx={{color: "#ec0707"}} />
              </IconButton>
              <Menu
                id="menu-appbar"
                anchorEl={anchorElNav}
                anchorOrigin={{
                  vertical: "bottom",
                  horizontal: "left",
                }}
                keepMounted
                transformOrigin={{
                  vertical: "top",
                  horizontal: "left",
                }}
                open={Boolean(anchorElNav)}
                onClose={handleCloseNavMenu}
                sx={{
                  display: { xs: "block", md: "none" },
                }}
              >
                {pages.map((page) => (
                  <MenuItem key={page} onClick={handleCloseNavMenu}>
                    <Typography textAlign="center">{page}</Typography>
                  </MenuItem>
                ))}
              </Menu>
            </Box>
            <AdbIcon
              sx={{
                display: { xs: "flex", md: "none" },
                mr: 1,
                color: "#ec0707",
              }}
            />
            <Typography
              variant="h5"
              noWrap
              component={Link}
              to="/browse"
              sx={{
                mr: 2,
                display: { xs: "flex", md: "none" },
                flexGrow: 1,
                fontFamily: "monospace",
                fontWeight: 700,
                letterSpacing: ".3rem",
                color: "#ec0707",
                textDecoration: "none",
              }}
            >
              NETFLIX GPT
            </Typography>
            <Box sx={{ flexGrow: 1, display: { xs: "none", md: "flex" } }}>
              {pages.map((page) => (
                <Button
                  key={page}
                  onClick={() => handleNavigation(page)}
                  sx={{ my: 2, color: "white", display: "block" }}
                >
                  {page}
                </Button>
              ))}
            </Box>
            <Button component={Link} to="search" variant="text" startIcon={<SearchIcon />} sx={{color:"#fff",mr:"10px"}}>
              Search
             </Button>
            <Box sx={{ flexGrow: 0 }}>
              <Tooltip title="profile">
                <IconButton onClick={handleOpenUserMenu} sx={{ p: 0}}>
                  <Avatar alt="Remy Sharp" src={AWATAR_URL} sx={{width:"33px",height:"33px",borderRadius:"5px" }} />
                </IconButton>
              </Tooltip>
              <Menu
                sx={{ mt: "45px" }}
                id="menu-appbar"
                anchorEl={anchorElUser}
                anchorOrigin={{
                  vertical: "top",
                  horizontal: "right",
                }}
                keepMounted
                transformOrigin={{
                  vertical: "top",
                  horizontal: "right",
                }}
                open={Boolean(anchorElUser)}
                onClose={handleCloseUserMenu}
              >
                {settings.map((setting) => (
                   <MenuItem key={setting} onClick={() => handleSettingClick(setting)}>
                   <Typography  variant="inherit" textAlign="center">
                   {setting === 'Profile' && user?.displayName ? user.displayName : setting}
                   </Typography>
                 </MenuItem>
                ))}
              </Menu>
            </Box>
          </Toolbar>
        </Container>
      </AppBar>
   
  );
}

export default NetFlixHeader;
